button {
    width: 175px;
    height: 56px;
    background-color: $darkCyan;
    color: #fff;
    font-size: 18px;
    border: none;
    outline: none;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    @include tablet {
        width: 217px;
    }
}
button:hover {
    background-color: $paleBlue;
}
button[disabled] {
    background-color: $lightGrey;
    color: $darkGreyBlue
}
